.divproject{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    overflow:scroll;
    column-gap: 0.5cm;
    row-gap: 0.2cm;
    padding:0.5cm;
    overflow-y: scroll;
    flex-wrap: wrap;
}

.card{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    row-gap: 0.2cm;
    column-gap: 0.4cm;
    cursor: pointer;
    width:7cm;
    height:fit-content;
    border-radius: 10px;
    padding:0.2cm 0.4cm;
    box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
    border: thin solid rgb(226, 226, 226);
    background-color: rgb(255, 255, 255);

}

.card:hover{
    transform: scale(1.05);
}

.iframediv{
    width:5cm;
    border: thin solid grey;
}

.p-name{
    font-size: 20px;
    padding: 2px;
}

.codeyear{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    width:95%;
    align-items: center;
}

.code{
    border: thin solid grey;
    border-radius: 10px;
    padding: 0.05cm;
    color:rgb(31, 31, 119);
    cursor: pointer;
}

.year{
    color: green;
}

.bio{
    font-size: 12px;
    color:rgb(55, 55, 55)
}
