.timeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
}

.timeline::after {
  content: '';
  position: absolute;
  width: 6px;
  background-color: rgb(85, 85, 85);
  top: 0;
  bottom: 0;
  /* left: 20%;  
   margin-left: -5px; */
}

.container {
  padding: 0.5cm 1cm;
  position: relative;
  background-color: inherit;
  width: fit-content;
}

.container::after {
  content: '';
  position: absolute;
  width: 25px;
  height: 25px;
  right: -17px; 
  background-color: rgb(228, 170, 224);
  border: 4px solid #ab56e8;
  top: 15px;
  border-radius: 50%;
  z-index: 1;
}

 .right {
  left: 1%; 
}


 .right::before {
  content: " ";
  height: 0;
  position: absolute;
  top: 22px;
  width: 0;
  z-index: 1;
  left: 30px; 
  border: medium solid rgb(79, 79, 79);
  border-width: 10px 10px 10px 0;
  border-color: transparent white transparent transparent;
}


.right::after {
  left: -16px;
}

.content {
  padding: 0.5cm 0.5cm;
  background-color: white;
  position: relative;
  border:1px solid antiquewhite;
  border-radius: 6px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
}

 @media screen and (max-width: 600px) {
  .timeline::after {
  left: 31px;
  }

  .container {
  width: 100%;
  padding-left: 70px;
  padding-right: 25px;
  }
  
  .container::before {
  left: 60px;
  border: medium solid white;
  border-width: 10px 10px 10px 0;
  border-color: transparent rgb(230, 230, 230) transparent transparent;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  }

.right::after {
  left: 15px;
  }

  .right {
  left: 0%;
  }
}

